<template>
  <div class="search-wrap">
    <form class="form-horizontal">
      <div class="form-group">
        <div class="col-sm-12">
          <div class="input-group">
            <input type="text"
                   class="form-control"
                   placeholder=""
                   v-model="sea.cont">
            <span class="input-group-btn">
              <button class="btn btn-primary" type="button" @click="search()"></button>
            </span>
          </div>
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label"></label>
        <div class="col-sm-5">
          <div class="input-group">
            <div class="input-group-addon"></div>
            <input type="date"
                   class="form-control"
                   placeholder=""
                   v-model="sea.time.start"
                   @change="search()">
          </div>
        </div>
        <div class="col-sm-5">
          <div class="input-group">
            <div class="input-group-addon"></div>
            <input type="date"
                   class="form-control"
                   placeholder=""
                   v-model="sea.time.end"
                   @change="search()">
          </div>
        </div>
      </div>
      <div class="form-group">
        <div class="col-sm-6">
          <div class="input-group">
            <div class="input-group-addon"></div>
            <select class="form-control" v-model="sea.amount" @change="search()">
              <option :value="AMO" v-for="(AMO, index) in amounts" :key="index">{{AMO}}</option>
            </select>
            <div class="input-group-addon"></div>
          </div>
        </div>
        <div class="col-sm-6">
          <div class="input-group">
            <div class="input-group-addon"></div>
            <select class="form-control" v-model="sea.type" @change="search()">
              <option :value="TYPE" v-for="(TYPE, index) in types" :key="index">{{TYPE}}</option>
            </select>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'businessCompleteSearchBar',
  data() {
    return {
      sea: {
        cont: '',
        time: {
          start: '',
          end: ''
        },
        amount: '',
        type: '',
      },
      amounts: [
        '',
        '0.1-2',
        '2-8',
        '8-20',
        '20'
      ],
      types: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ]
    };
  },
  methods: {
    search() {
      this.$emit('search', this.sea);
    }
  }
};
</script>

<style style="sass" scoped>
.search-wrap {
  margin-top: 30px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
}
</style>



