<script>
import axios from 'axios';
import qs from 'qs';
import modal from '../../../component/modal.vue'

export default {
	name: 'bidInfoEdit',
	data() {
		return {
			project: this.iniProject,
			cancelModal: false,
			commonwealthShow: false
		}
	},
	computed: {
		zjsBiddingContent() {
			if (this.office === "") {
				return true;
			} else {
				return false;
			}
		},
		zjsFundSourceShow() {
			if (this.office === "") {
				return true;
			} else {
				return false;
			}
		},
		kjsBiddingContent() {
			if (this.office === "") {
				return true;
			} else {
				return false;
			}
		},
		pgsBiddingContent() {
			if (this.office === "") {
				return true;
			} else {
				return false;
			}
		},
		swsBiddingContent() {
			if (this.office === "") {
				return true;
			} else {
				return false;
			}
		},
		OwnershipStructure() {
			if (this.office === "") {
				return false;
			} 
			if (this.office === "") {
				return true;
			} 
			if (this.office === "") {
				return true;
			} 
			if (this.office === "") {
				return true;
			} 
		},
		scale() {
			if (this.office === "") {
				return true;
			}
		},
		scaleOther() {
			if (this.office !== "") {
				return true;
			}
		}
	},
	props: ['iniProject','office'],
	methods: {
		submit(project) {
			this.$emit('submit',project);
		},
		saveDraft(project) {
			this.$emit('saveDraft',project);
		},
		cancel() {
			this.cancelModal = true;
		},
		quxiao() {
			this.cancelModal = false;
		},
		queding() {
			//
			this.$emit('quedingDelete',this.project.id);
		},
		delBasicFee(index) {
			this.$emit('delBasicFee',index);
		},
		addBasicFee() {
			this.$emit('addBasicFee');
		},
		delEfficiencyFee(index) {
			this.$emit('delEfficiencyFee',index);
		},
		addEfficiencyFee() {
			this.$emit('addEfficiencyFee');
		},
		isCommonwealth() {
			this.commonwealthShow = true;
		},
		notCommonwealth() {
			this.commonwealthShow = false;
		}
	},
	created() {

	},
	components: {
		modal
	}
};
</script>


